import React from 'react';
import PropTypes from 'prop-types';
import { Row, Col } from 'reactstrap';

import { SynopsisContainer } from './style';

const propTypes = {};

const defaultProps = {};

export default function SynopsisSection({ poster }) {
  return (
    <SynopsisContainer>
      <Row>
        <Col xs="9" className="no-padding bordered">
          Description
        </Col>
        <Col xs="3" className="no-padding bordered">
          <img src={poster} alt=""/>
        </Col>
      </Row>
    </SynopsisContainer>
  );
}

SynopsisSection.propTypes = propTypes;
SynopsisSection.defaultProps = defaultProps;